import React, { useState, useEffect } from 'react';

const AddProjectInterface = (
    {
        newProjectDescription,
        setNewProjectDescription,
        addProject
    }
) => {

    return (
        <div className='containerBox'>
                    <input
                        className='size25 bold color-lite bg-dark width-100-percent'
                        type="text"
                        value={newProjectDescription}
                        onChange={(e) => setNewProjectDescription(e.target.value)}
                        onKeyDown={(e) => {
                            if (e.key === 'Enter') {
                                e.preventDefault();
                                addProject();
                            }
                        }}
                    />
                    <div className='containerBox bold bg-lite color-light size25 button mt-20 mb-20' onClick={addProject}>
                        Add Log
                    </div>
                </div>
    )
}
export default AddProjectInterface;               